:root {
  --one: #e24e42; 
  --two: #D9B310;
  --three: #EB6E80;
  --four: #008F95;
  --five: #2B2A2A;
}

.btn-outline-primary{
  color: #e24e42;
  color: var(--one);
  border-color: #EB6E80;
  border-color: var(--three);
  padding: 8px 30px;
  border-radius: 30px
}

.btn-outline-primary:hover{
  background-color: #EB6E80;
  background-color: var(--three);
  border-color: #e24e42;
  border-color: var(--one);
}

footer {
    background: #00252e
}

footer .socials i {
    padding: 10px 20px 0 0;
    font-size: 40px
}

footer .socials i:hover {
    color: var(--three)
}

.btn-outline-submit{
    background-color: var(--three);
    color: #fff;
    border-color: var(--three);
    padding: 8px 30px;
    border-radius: 0 20px 20px 0;
  }
  
  .btn-outline-submit:hover{
    background-color: var(--one);
    border-color: var(--one);
  }

  .copyright {
      color: var(--five);
      background-color: var(--three);
      text-align: center;
      padding: 20px
  }


